import { useState } from "react";

function Nav() {
    const [curr,setState]=useState(true)
    return <>
        <div className="Nav_main">
        <div className="dropdown">
            <div className="icon" onClick={()=>setState(!curr)}><i  className="zmdi zmdi-menu"></i></div>
            <div className={curr? "active":"nav"}>
            <div className="divi">Home</div>
            <div className="divi"><a href="index.html" className="href">Registration</a></div>
            <div className="divi">Login</div>
            <div className="divi">About Us</div>
            <div className="divi">Contact</div>
            </div>
        </div>
            <div className="nav_logo">
                <img src="https://secureservercdn.net/160.153.137.40/q6q.af3.myftpupload.com/wp-content/uploads/2019/12/0963-1.png" alt="some" className="logo"/>
            </div>
            <div className="nav_items">
            <div className="divi">Home</div>
            <div className="divi"><a href="index.html" className="href">Registration</a></div>
            <div className="divi">Login</div>
            <div className="divi">About Us</div>
            <div className="divi">Contact</div>
            </div>
        </div>
        
</>
    
}
export default Nav;